.nav{
    width: 30%
}

.nav ul{
    display: flex;
    list-style: none;
}

.nav li{
    width: 33%;
    text-align: center;
    align-self: baseline;
    
   }

.nav a{
    color: #4fa6b0;
        /* font-family:var(--font-button) ; */
        font-size: x-large;
        font-weight: 500;
        text-decoration: none;
        font-size: large
}

.nav-link.active{
    color: #a1c116
}

@media screen and (max-width:992px) {
    .nav{
        width: 100%;
        margin-top: 1%;
        
    }
    .nav ul{
        justify-content: space-evenly;
    }

}